.card {
    width: 175px;
    font-size: 12px;
    flex: 0 0 auto;         /* ADD */
    display: flex;          /* ADD */
    flex-direction: column; /* ADD */
    position: relative;
}
.card--image {
    width: 100%;
    border-radius: 9px;
}
.card--stats {
      display: flex;
      align-items: center ;
}
.card--star {
    height: 14px;
}
.grey {
    color: #918E9B
    ;
}
.bold {
    font-weight: bold;
}
.cards-list {
    display: flex;
    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;
}
.card--title {
    overflow: hidden;
    text-overflow: ellipsis;
}
.card--price {
    margin-top: auto;
}
section {
    padding: 20px;
}
.card--badge {
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}